.van-loading {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: #c8c9cc;
  color: var(--loading-spinner-color, #c8c9cc);
}
.van-loading__spinner {
  position: relative;
  box-sizing: border-box;
  width: 1.28rem;
  width: var(--loading-spinner-size, 1.28rem);
  max-width: 100%;
  max-height: 100%;
  height: 1.28rem;
  height: var(--loading-spinner-size, 1.28rem);
  -webkit-animation: van-rotate 0.8s linear infinite;
          animation: van-rotate 0.8s linear infinite;
  -webkit-animation: van-rotate var(--loading-spinner-animation-duration, 0.8s) linear infinite;
          animation: van-rotate var(--loading-spinner-animation-duration, 0.8s) linear infinite;
}
.van-loading__spinner--spinner {
  -webkit-animation-timing-function: steps(12);
          animation-timing-function: steps(12);
}
.van-loading__spinner--circular {
  border: 0.04267rem solid transparent;
  border-top-color: currentColor;
  border-left-color: currentColor;
  border-radius: 100%;
}
.van-loading__text {
  margin-left: 0.34133rem;
  margin-left: var(--padding-xs, 0.34133rem);
  color: #969799;
  color: var(--loading-text-color, #969799);
  font-size: 0.59733rem;
  font-size: var(--loading-text-font-size, 0.59733rem);
  line-height: 0.85333rem;
  line-height: var(--loading-text-line-height, 0.85333rem);
}
.van-loading__text:empty {
  display: none;
}
.van-loading--vertical {
  -webkit-flex-direction: column;
          flex-direction: column;
}
.van-loading--vertical .van-loading__text {
  margin: 0.34133rem 0 0;
  margin: var(--padding-xs, 0.34133rem) 0 0;
}
.van-loading__dot {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.van-loading__dot::before {
  display: block;
  width: 0.08533rem;
  height: 25%;
  margin: 0 auto;
  background-color: currentColor;
  border-radius: 40%;
  content: ' ';
}
.van-loading__dot:nth-of-type(1) {
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
  opacity: 1;
}
.van-loading__dot:nth-of-type(2) {
  -webkit-transform: rotate(60deg);
          transform: rotate(60deg);
  opacity: 0.9375;
}
.van-loading__dot:nth-of-type(3) {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  opacity: 0.875;
}
.van-loading__dot:nth-of-type(4) {
  -webkit-transform: rotate(120deg);
          transform: rotate(120deg);
  opacity: 0.8125;
}
.van-loading__dot:nth-of-type(5) {
  -webkit-transform: rotate(150deg);
          transform: rotate(150deg);
  opacity: 0.75;
}
.van-loading__dot:nth-of-type(6) {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  opacity: 0.6875;
}
.van-loading__dot:nth-of-type(7) {
  -webkit-transform: rotate(210deg);
          transform: rotate(210deg);
  opacity: 0.625;
}
.van-loading__dot:nth-of-type(8) {
  -webkit-transform: rotate(240deg);
          transform: rotate(240deg);
  opacity: 0.5625;
}
.van-loading__dot:nth-of-type(9) {
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
  opacity: 0.5;
}
.van-loading__dot:nth-of-type(10) {
  -webkit-transform: rotate(300deg);
          transform: rotate(300deg);
  opacity: 0.4375;
}
.van-loading__dot:nth-of-type(11) {
  -webkit-transform: rotate(330deg);
          transform: rotate(330deg);
  opacity: 0.375;
}
.van-loading__dot:nth-of-type(12) {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
  opacity: 0.3125;
}
@-webkit-keyframes van-rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes van-rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
